import React, {useEffect} from "react";
import { useDispatch, useSelector } from "react-redux";
import Accounts from "../components/accounts";
import {TAGGSHOP_GET_USER_ACCOUNTS_DATA } from '../../actions/sceneActions'

 function AccountsPage() {
  const userAccountData = useSelector(state => state.userAccountData)
  const dispatch = useDispatch()

useEffect(() => {
  dispatch(TAGGSHOP_GET_USER_ACCOUNTS_DATA());
}, [dispatch]);

  return  <Accounts accountData={userAccountData} />
}


export default AccountsPage;