import React from "react";
import { CLOUDPATH_TAGGSHOP } from "../../../../../../actions/api";

const Expensive = ({acceptOfferSubscription, offerRequest}) => {
    return (
        <div className="bg-blue rounded expensive_content_box">
            <div className="text-center">
                <div className="expensive_image_box">
                    <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/cancel-subscription/expensive-image.png`} alt="alternative icon" width="234" height="235" /> 
                </div>
                <p className="cSubs_tooExpensive_para">We understand cost is important and we want to ensure that our services are beneficial for all our customer's journeys.</p>
                <p className="cSubs_tooExpensive_heading">Hence, we would like to offer you an exclusive <b>10%</b> discount on your next billing cycle. Claim Now!</p>

                <button className={`btn btn-primary ${offerRequest ? 'spinner spinner-right disabled' : ''}`} onClick={() => acceptOfferSubscription() }>Accept This Offer</button>

            </div>
        </div>
    )
}

export default Expensive;