import React, { Component } from 'react'
import { CLOUDPATH_TAGGSHOP } from '../../../../actions/api'

export default class Subscription_cancel_popup extends Component {
    render() {
        const { planExpireDate, closePopup} = this.props
        return (
            <div id="cancelSubscription" className="modal fade tabbable show" data-backdrop="static" tabIndex="-1" role="dialog" aria-labelledby="staticBackdrop" aria-modal="true" style={{display:'block', background: 'rgba(0,0,0,0.5)'}}>
                <div className="modal-dialog modal-dialog-centered  modal-md">
                    <div className="modal-content">
                        <div className="modal-body">
                            <button type="button" className="close" data-bs-dismiss="modal" onClick={()=>closePopup()}><i aria-hidden="true" className="ki ki-close"></i></button>
                            <div className="p-8">
                                <div className="pb-8 text-center">
                                    <img className="img-fluid mb-10" src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/svg/cancel-subcription.svg`} style={{width:'100px'}}/>
                                    <h3 className="text-center mb-5">Your Subscription Has Been Cancelled</h3>
                                    <p className="text-center">
                                        We have processed your request of deactivation. Your Tagshop accounts subscription will
                                        remain
                                        active until <span id="expdate">{planExpireDate}</span> and you wont be charged henceforth.
                                    </p>
                                    <p>
                                        Although we wished for you to stay longer. So, if you change your mind, and want to continue
                                        using Tagbox, you can renew or upgrade your existing plan.
                                    </p>
                                    <p>In case of any query or suggestion reach out to us at: <b>support@tagshop.ai</b></p>
                                </div>
                                <div className="text-center">
                                    <button type="button" className="btn btn-primary upgPlan" onclick="Intercom('show');" data-bs-dismiss="modal" onClick={()=>closePopup()}>Share Feedback</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
