import React, { Component } from 'react'
import moment from 'moment';
import parse from 'html-react-parser';
export default class AccountInfo extends Component {
    render() {
        const { accountData, accountCard } = this.props;
        const dateSince = moment(accountData.AccountProfile[0].MemberSince).format('Do MMM YYYY');
        
        return (
            <>
                <h4 className="text-dark font-weight-bold my-1 mb-4">Account Profile</h4>
                <div className="row">
                    <div className="col-lg-6 col-md-6">
                        <div className="card card-custom mb-8 bgi-no-repeat" style={{ backgroundPosition: 'right top', backgroundSize: '30% auto', backgroundImage: 'url(https://cloud.taggbox.com/taggshop/app/assets/media/svg/abstract-1.svg)' }}>
                            <div className="card-body">
                                <div className="d-flex align-items-center">
                                    <div className="me-4">
                                        <span className="svg-icon svg-icon-primary svg-icon-4x">
                                            <svg id="Group_4562" data-name="Group 4562" xmlns="http://www.w3.org/2000/svg" width="55" height="55" viewBox="0 0 55 55">
                                                <path id="Path_5262" data-name="Path 5262" d="M17.167,21.333a9.167,9.167,0,1,1,9.167-9.167A9.167,9.167,0,0,1,17.167,21.333Z" transform="translate(10.333 3.875)" fill="#2b2b2b" opacity="0.3"></path>
                                                <path id="Path_5263" data-name="Path 5263" d="M3,29.5C3.89,18.561,12.767,13,23.587,13c10.972,0,19.987,5.255,20.658,16.5a1.574,1.574,0,0,1-1.722,1.833H4.667A2.537,2.537,0,0,1,3,29.5Z" transform="translate(3.875 16.792)" fill="#2b2b2b"></path>
                                            </svg>
                                        </span>
                                    </div>
                                    <div className="d-flex flex-column">
                                        <h5 className="mb-2 text-primary">Member Since</h5>
                                        <div className="text-dark-75">{dateSince}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6 col-md-6">
                        <div className="card card-custom mb-8 bgi-no-repeat" style={{ backgroundPosition: 'right top', backgroundSize: '30% auto', backgroundImage: 'url(https://cloud.taggbox.com/taggshop/app/assets/media/svg/abstract-1.svg)' }}>
                            <div className="card-body">
                                <div className="d-flex align-items-center">
                                    <div className="me-4">
                                        <span className="svg-icon svg-icon-primary svg-icon-4x">
                                            <svg id="Group_4543" data-name="Group 4543" xmlns="http://www.w3.org/2000/svg" width="55" height="53.942" viewBox="0 0 55 53.942">
                                                <rect id="Rectangle_2142" data-name="Rectangle 2142" width="53.942" height="53.942" fill="none"></rect>
                                                <path id="Path_5259" data-name="Path 5259" d="M.3,0,0,19.935,4.768,24.7l4.768-4.768L6.357,16.756,9.443,13.67,6.265,10.492,9.536,7.221,9.258,0Z" transform="translate(25.334 24.113) rotate(45)" fillRule="evenodd" opacity="0.3"></path>
                                                <path id="Path_5260" data-name="Path 5260" d="M13.486,26.971A13.486,13.486,0,1,1,26.971,13.486,13.486,13.486,0,0,1,13.486,26.971Zm.432-13.293a3.371,3.371,0,1,0-3.371-3.371A3.371,3.371,0,0,0,13.918,13.678Z" transform="translate(35.928 1.124) rotate(45)" fillRule="evenodd"></path>
                                            </svg>
                                        </span>
                                    </div>
                                    <div className="d-flex flex-column overflow-hidden">
                                        <h5 className="mb-2 text-primary">User Key</h5>
                                        <div className="text-dark-75 text-wrap">
                                            {accountData.AccountProfile[0].UserKey}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                   {accountCard && accountCard.PaymentMethod == 1 ? 
                    <div className="col-lg-6 col-md-6 accountInfoBox accCardInfo">
                        <div className="card card-custom mb-8 bgi-no-repeat" style={{ backgroundPosition: "right top", backgroundSize: "30% auto", backgroundImage: "url('https://cloud.taggbox.com/taggshop/app/assets/media/svg/abstract-1.svg')"}}>
                            <div className="card-body">
                                <div className="d-flex align-items-center">
                                    <div className="mr-6">
                                        <span className="svg-icon svg-icon-primary svg-icon-4x">
                                            <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                                <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                                    <rect x="0" y="0" width="24" height="24"></rect>
                                                    <rect fill="#000000" opacity="0.3" x="2" y="5" width="20" height="14" rx="2"></rect>
                                                    <rect fill="#000000" x="2" y="8" width="20" height="3"></rect>
                                                    <rect fill="#000000" opacity="0.3" x="16" y="14" width="4" height="2" rx="1"></rect>
                                                </g>
                                            </svg>
                                        </span>
                                    </div>

                                    <div className="d-flex flex-column w-100">
                                        <h5 className="mb-3 text-primary">Payment Method</h5>
                                        <div className="d-flex align-items-center justify-content-between">
                                            <div className="text-dark-75">
                                                <span id="cVisa" className="mr-2">{accountCard.CardType}</span><span id="cLastDigits">**** **** ****{accountCard.LastDigits}</span>
                                            </div>
                                            <a id="2UpdateBtn" target="_blank" className="ladda-button submitButton font-weight-bold btn btn-primary btn-sm btn-pill" href={accountCard.link}>Update card </a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div> :''}

                </div>
                <h4 className="text-dark font-weight-bold my-1 mb-4">Plan Details</h4>
                <div className="row">

                    {accountData && accountData.PlanDetails && accountData.PlanDetails.map((item, index) => (
                       item.divHide==0 ? <div className="col-xl-2-5 col-md-4 col-sm-6 " key={index}>
                            <div className="card card-custom card-stretch overflow-hidden gutter-b account_status_card">
                                {item.trueIcon ? item.status ?
                                    <div className="card_status status_checked">
                                        <span className="svg-icon svg-icon-white svg-icon-lg">
                                            <span className="svg-icon svg-icon-white svg-icon-lg">
                                                <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                                    <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                                        <polygon points="0 0 24 0 24 24 0 24"></polygon>
                                                        <path d="M6.26193932,17.6476484 C5.90425297,18.0684559 5.27315905,18.1196257 4.85235158,17.7619393 C4.43154411,17.404253 4.38037434,16.773159 4.73806068,16.3523516 L13.2380607,6.35235158 C13.6013618,5.92493855 14.2451015,5.87991302 14.6643638,6.25259068 L19.1643638,10.2525907 C19.5771466,10.6195087 19.6143273,11.2515811 19.2474093,11.6643638 C18.8804913,12.0771466 18.2484189,12.1143273 17.8356362,11.7474093 L14.0997854,8.42665306 L6.26193932,17.6476484 Z" fill="#000000" fillRule="nonzero" transform="translate(11.999995, 12.000002) rotate(-180.000000) translate(-11.999995, -12.000002) "></path>
                                                    </g>
                                                </svg>
                                            </span>
                                        </span>
                                    </div> : 
                                    <div className="card_status status_unchecked">
                                    <span className="svg-icon svg-icon-white svg-icon-lg">
                                        <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                            <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                                <g transform="translate(12.000000, 12.000000) rotate(-45.000000) translate(-12.000000, -12.000000) translate(4.000000, 4.000000)" fill="#000000">
                                                    <rect x="0" y="7" width="16" height="2" rx="1"></rect>
                                                    <rect transform="translate(8.000000, 8.000000) rotate(-270.000000) translate(-8.000000, -8.000000) " x="0" y="7" width="16" height="2" rx="1"></rect>
                                                </g>
                                            </g>
                                        </svg>
                                    </span>
                                </div>
                                :''}

                                <div className="card-body text-center">
                                    <span className="plan_icon svg-icon-3x">
                                        {parse(item.icon)}
                                    </span>
                                    <h5 className="card-title font-weight-bold mt-4 mb-2 d-block">{item.title}</h5>
                                    {item.total ? <span className="font-weight-bold text-muted  font-size-sm">using <span className="text-dark">{item.using}</span> out of <span className="text-dark">{item.total}</span></span>:''}
                                    <span className="font-weight-bold text-muted  font-size-sm">{item.viewMonth} </span>
                                </div>
                            </div>
                        </div>:''

                    ))}


                  




                </div>


            </>
        )
    }
}
