import React from "react";

const options = [
    "Didn't find the features useful?",
    "Got concerns regarding security and compliance.",
    "Didn't find the UI attractive.",
    "Frequent bugs and errors.",
    "Anything else."
]
const Technical = ({ setTechnicalValue, technicalValue, error, setError }) => {
    const { feedbackOption, feedbackContent } = technicalValue;

    return (
        <div className="bg-blue rounded technical_content_box">
            <div className="">
                <h6>Could you specify the issue you are experiencing with Tagshop?</h6>
                <ul className="options-list">
                    {options.map(item => <li key={item}>
                        <div className="radio d-inline-flex" >
                            <input type="radio" name="technical" checked={feedbackOption == item} onChange={() => {
                                setTechnicalValue(prevState => ({ ...prevState, feedbackOption: item }));
                                setError(false);
                            }} />
                            <label className="radio-label"><p className="mb-0">{item}</p></label>
                        </div>
                    </li>)}
                    {(feedbackOption != 'Anything else.' && error) && <span id="feedsfield2error" className="errorMessage required">Please select an option</span>}
                    {feedbackOption == 'Anything else.' && <><textarea className="form-control" placeholder="Enter your feedback" rows="3" value={feedbackContent} onChange={(e) => {
                        setTechnicalValue(prevState => ({ ...prevState, feedbackContent: e.target.value }));
                        setError(false);
                    }} />
                        {(error && feedbackContent.length == 0) ? <span id="feedsfield2error" className="errorMessage required">This field is required</span> : null}
                    </>}
                </ul>
                <div className="alert alert-custom align-items-start">
                    <div className="alert-icon py-1 align-items-start pe-0">
                        <span className="svg-icon svg-icon-1 svg-icon-info me-2">
                            <svg className="w-30px h-30px" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <rect opacity="0.3" x="2" y="2" width="20" height="20" rx="10" fill="currentColor"></rect>
                                <rect x="11" y="14" width="7" height="2" rx="1" transform="rotate(-90 11 14)" fill="currentColor"></rect>
                                <rect x="11" y="17" width="2" height="2" rx="1" transform="rotate(-90 11 17)" fill="currentColor"></rect>
                            </svg>
                        </span>
                        <div className="d-flex justify-content-center align-items-start flex-column flex-grow-1">
                            <p className="mb-4">Apologies for any inconvenience! We want to ensure a smooth experience. Could you share more details about the technical issues you've encountered? Our team is here to assist.</p>
                        </div>
                    </div>
                </div>
                <div className="d-flex">
                    <button className="btn btn-outline me-3" onClick={window.Intercom ? (e) =>{window.Intercom('show') }: ()=>{}}>
                        Chat Now
                    </button>
                    <button className="btn btn-primary" onClick={() => window.open('https://meetings.hubspot.com/prakhar-from-taggshop?uuid=d5aa4dfa-9d30-420f-9449-2bb39e55e909', '_blank')}>
                        Book A Call
                    </button>
                </div>
            </div>
        </div>
    )
}

export default Technical;