import React from "react";

const CancellationList = (props) => {
    const { handleStatusChange, data, planId } = props;
    
    return (
        <div className="small_container">
            <h3 className="cancellation-main-heading">Please let us know the reason you want to cancel your Tagshop Subscription.</h3>
            <div className="cancellation-list mt-5">
                <div className="cancellation-item">
                    {
                        data.map((item, index) => {   
                            return !((planId == 53 || planId == 58) && item.interHeading == 'Too expensive') ? <div className="card d-flex cursor-pointer" key={index} onClick={() => handleStatusChange(item, item.type)}>
                            <div className="card-inner d-flex flex-wrap align-items-center">
                                <div className="card-icon"> {item.icon} </div>
                                <div className="card-details">
                                    <h5 className="cs_card-heading" >{item.interHeading}</h5>
                                    <p className="cs_card-para">{item.text}</p>
                                </div>
                            </div>
                        </div> : null
                        })
                    }
                </div>
            </div>
        </div>
    )
}


export default CancellationList;