import React, { Component } from "react";
import AccountInfo from "./elements/AccountInfo";
import ProductSubscriptions from "./elements/ProductSubscriptions";
import SocialAccounts from "./elements/SocialAccounts";
import { connect } from 'react-redux';
import { TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA, TAGGSHOP_SOCIAL_ACCOUNT_DATA  } from '../../../actions/sceneActions'
import AccountsSkeleton from './elements/skeleton/AccountsSkeleton';
import ProductSubscriptionsSkeleton from './elements/skeleton/ProductSubscriptionsSkeleton';
import SocialAccountSkeleton from './elements/skeleton/SocialAccountSkeleton';
import { GET_LOCAL, TOAST_ERROR, TOAST_SUCCESS } from "../../../utils";
import { addAccountFromId } from "../../../utils/addAccount";

const channel = new BroadcastChannel('connect');
class AccountsPageDashboard extends Component{
    constructor() {
        super();
        this.controller = new AbortController();

    }

    handleMessage = async (event) => {
        console.log(event)
        const networkId = GET_LOCAL('networkId')
        console.log('networkId', networkId)
        if (typeof event.data == 'string') {
            const { error, code } = JSON.parse(event.data);
            if (code) {
                await addAccountFromId(networkId, code, this.props.TAGGSHOP_SOCIAL_ACCOUNT_DATA)
            }
        }
        else {
            const { error, code } = event.data;
            if (event.origin.includes("tagshop.ai") || event.origin.includes("localhost")) {
                if (code) {
                    await addAccountFromId(networkId, code, this.props.TAGGSHOP_SOCIAL_ACCOUNT_DATA)
                }
            }
        }
    };
    componentDidMount() {
        this.props.TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA();
        this.props.TAGGSHOP_SOCIAL_ACCOUNT_DATA();
        const signal = this.controller.signal;
        window.addEventListener("message", this.handleMessage, { signal });
        channel.addEventListener("message", this.handleMessage);
    }
    componentWillUnmount(){
        // window.removeEventListener("message", this.handleMessage);
        this.controller.abort()
        channel.close()
    }
    render(){

        const { accountData, productSubscribData, userAccountData , authorized} = this.props;
        const activeTab = (window.location.href).includes('#') ? (window.location.href).split("#")[1] : "accountInfo"
        return <div className="account_info_wrap">
        <div className="subheader subheader_main fixed_subheader subheader-transparent dual_subheader" id="kt_subheader">
            <div className="sub_header_top">
                <div className="container-fluid">
                    <div className="d-flex align-items-center justify-content-between flex-wrap flex-sm-nowrap">
                        <ul className="nav nav-tabs nav-tabs-space-lg flex-nowrap align-content-end nav-tabs-line nav-tabs-bold top_nav_tabs" role="tablist">
                            <li className="nav-item mr-3">
                                <a className={`nav-link ${activeTab == 'accountInfo' ? "active" : "" }`} data-bs-toggle="tab" href="#accountInfo">
                                    <span className="nav-text text-nowrap">Account Info</span>
                                </a>
                            </li>
                            <li className="nav-item mr-3">
                                <a className={`nav-link ${activeTab == 'productSubscriptions' ? "active" : "" }`} data-bs-toggle="tab" href="#productSubscriptions">
                                    <span className="nav-text text-nowrap">Product Subscriptions</span>
                                </a>
                            </li>
                            <li className="nav-item">
                                <a className={`nav-link ${activeTab == 'connectedAccounts' ? "active" : "" }`} data-bs-toggle="tab" href="#connectedAccounts">
                                    <span className="nav-text text-nowrap">Social Accounts</span>
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <div className="d-flex flex-column-fluid main_content subheader_content">
            <div className="container">
                <div className="screen_wrapper pb-10 pt-5">
                    <div className="tab-content">
                        <div id="accountInfo" className={`tab-pane fade ${activeTab == 'accountInfo' ? "show active" : "" }`}>
                        {accountData && accountData.accountInfo ? <AccountInfo accountData={accountData.accountInfo} accountCard={accountData.accountCardData}/>: <AccountsSkeleton />}
                        </div>

                        <div id="productSubscriptions" className={`tab-pane fade ${activeTab == 'productSubscriptions' ? "show active" : "" }`}>
                        {productSubscribData ? <ProductSubscriptions  productSubscribData={productSubscribData} authorized={authorized}/>:<ProductSubscriptionsSkeleton />}
                        </div>

                        <div id="connectedAccounts" className={`tab-pane fade ${activeTab == 'connectedAccounts' ? "show active" : "" }`}>
                        { !userAccountData.accountsLoading ? <SocialAccounts />:<SocialAccountSkeleton />}
                        </div>
                        
                    </div>
                </div>
            </div>
        </div>





  </div>
    }
    
    }
    


    const mapStateToProps = state => {
        return {
            productSubscribData: state.userAccountData.productSubscriptions,
            userAccountData: state.userAccountData,
            authorized: state.authorized,
        }
      }
      
      const mapDispatchToProps = dispatch => {
        return {
            TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA: () => dispatch(TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA()),
            TAGGSHOP_SOCIAL_ACCOUNT_DATA: () => dispatch(TAGGSHOP_SOCIAL_ACCOUNT_DATA()),
        }
      }
      
      export default connect(mapStateToProps, mapDispatchToProps)(AccountsPageDashboard);



