import React from "react";
import CancellationList from "./components/CancellationList";
import CancelSubComponents from "./components";
import ContinueToCancel from "./components/ContinueToCancel";
import { connect } from "react-redux";
import { CLOUDPATH_TAGGSHOP } from "../../../../../actions/api";

var dataJson = [
    {
        type: 1,
        interHeading: 'Found a better alternative', text: 'Did you find a product with better features or price than ours?',
        icon: <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/svg/icons/alternative-icon.svg`} alt="alternative icon" width="47" height="47" /> 
    },
    {
        type: 2,
        interHeading: 'Too expensive', text: 'Did the price we offer not match your expectations?', 
        icon: <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/svg/icons/expensive-icon.svg`} alt="Expensive icon" width="47" height="47" /> 
    },
    {
        type: 3,
        interHeading: 'Technical issues', text: 'Finding it hard to avoid technical glitches?', 
        icon: <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/svg/icons/technical-icon.svg`} alt="Technical icon" width="47" height="47" /> 
    },
    {
        type: 4,
        interHeading: 'Hard to setup and use', text: 'Do you need any help with setting up the Gallery to get the most out of it?', 
        icon: <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/svg/icons/setup-icon.svg`} alt="Setup icon" width="47" height="47" /> 
    },
    {
        type: 5,
        interHeading: 'Others (please specify)', text: "Is there anything else that's bothering you?", 
        icon: <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/svg/icons/others-icon.svg`} alt="Others icon" width="47" height="47" /> 
    }
]

class Cancellation extends React.Component {

    constructor(props) {
        super(props);
        this.state ={ 
            pageData: {}, 
            pageStatus: 0, 
            acceptOfferData: {},
            cancellationType: null
        };
    }


    handleStatusChange = (data, pageType) => {
        this.setState({ pageData: data, pageStatus: pageType, cancellationType: null });
    };

    updateCancellationType = (type)=>{
        this.setState({ cancellationType: type });
    }

    acceptOfferSubscription = (value) => { this.setState({ acceptOfferData: value }) }
    render() {
        const { pageData, pageStatus, acceptOfferData, cancellationType } = this.state;
        const { handleParentStatus, cancellationDetails, closePopup , productId , planId , authorized} = this.props; 
        // const { planId, productId } = (cancellationDetails && Object.keys(cancellationDetails).length > 0) ? cancellationDetails.productCancellation : { planId: 0, productId: 6 };
        const planDetails = {planId, productId};
        return (
            <div className="d-flex flex-column-fluid cancellation-main bg-white">
                { pageStatus == 0 && <button className="btn btn-icon btn-clean btn-lg me-4 btn-square close-modal" onClick={() => handleParentStatus(false)}>
                    <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="28" height="28" viewBox="0 0 24 24" version="1.1">
                        <g transform="translate(12.000000, 12.000000) rotate(-45.000000) translate(-12.000000, -12.000000) translate(4.000000, 4.000000)" fill="#545454">
                            <rect x="0" y="7" width="16" height="2" rx="1"></rect>
                            <rect transform="translate(8.000000, 8.000000) rotate(-270.000000) translate(-8.000000, -8.000000) " x="0" y="7" width="16" height="2" rx="1"></rect>
                        </g>
                    </svg>
                </button> }
                <div className="container">
                    <div className={`small_container_main  ${pageData?.type ? 'expand_view':''} ${cancellationType ? 'cs_thank_youPage':''}`}>
                        <div className="justify-content-center align-items-center p-5 cs-100">
                            <div className="my-5 cs-100">
                                {cancellationType ?
                                    <ContinueToCancel pageStatus={cancellationType} acceptOfferData={acceptOfferData} handleParentStatus={handleParentStatus} />
                                    :
                                    (pageData && Object.keys(pageData).length > 0) ? 
                                    <CancelSubComponents pageData={pageData} handleStatusChange={this.handleStatusChange} updateCancellationType={this.updateCancellationType} acceptOfferSub={this.acceptOfferSubscription} planDetails={planDetails} authorized={authorized}/> : 
                                    <CancellationList handleStatusChange={this.handleStatusChange} data={dataJson} planId={planId} />
                                }
                            </div>
                        </div>
                        {
                        pageStatus == 0 &&
                        <div className="cancellation-footer position-absolute w-100 bottom-0 left-0 py-5 bg-white">
                            <div className="small_container">
                                <div className="text-right">
                                    <button className="btn btn-primary" onClick={() => handleParentStatus(false) }>Keep my subscription</button>
                                </div>
                            </div>
                        </div>
                    }
                    </div>
                </div>    
            </div> 
        );
    }
}

const mapStateToProps = (state) => {
    return {
        cancellationDetails: state.userAccountData,
    }
}

export default connect(mapStateToProps)(Cancellation);