import React, { Component } from 'react'
import { Modal } from 'react-bootstrap';
import moment from 'moment';
import { CLOUDPATH_TAGGSHOP, HOST_IMG_URL_TAGGSHOP } from '../../../../actions/api';

export default class InvoicePopup extends Component {

    render() {
        const { showInvoice, invoiceInfo, closeInvoicePopup, downloadInvoicePDF, invoiceId } = this.props
        const invoiceView = invoiceInfo.invoiceView
        const logoInvoice =`${CLOUDPATH_TAGGSHOP}/app/assets/media/logos/invoice_logo.svg` ;

        return (
            <Modal size="lg" dialogClassName="modal-dialog-centered" show={showInvoice} onHide={closeInvoicePopup}> 
                <Modal.Header>
                    <Modal.Title>Invoice</Modal.Title>
                    <button type="button" className="close" data-bs-dismiss="modal" onClick={closeInvoicePopup}>
                        <i aria-hidden="true" className="ki ki-close"></i>
                    </button>
                </Modal.Header>
                <Modal.Body>
                    {invoiceView ?
                        <div id="invoice_page" className="invoice_page">
                            <div className="mb-10 p-0 p-lg-5">
                                <div className="flex-column d-flex justify-content-center py-5">
                                    <div className="d-flex justify-content-between">
                                        <a className="mb-10 pr-8 mr-8 d-flex flex-grow-1">
                                            <img className="img-fluid" src={logoInvoice} alt="tagshop" style={{ maxWidth: '130px' }} />
                                        </a>
                                        <div className="d-flex flex-column align-items-md-end px-0">
                                            <h5>INVOICE</h5>
                                            <span className=" d-flex flex-column align-items-md-end opacity-70">
                                                <span className="font-weight-bolder text-dark">Invoice : #{invoiceView.invoiceId}</span>
                                                {invoiceView.gstinCompany ? <span className="font-weight-bolder text-dark">GSTIN : {invoiceView.gstinCompany}</span>:''}
                                            </span>
                                        </div>
                                    </div>
                                    <div className="border-bottom w-100"></div>
                                    <div className="d-flex justify-content-between pt-6">
                                        <div className="d-flex flex-column flex-root">
                                            <span className="font-weight-bolder mb-2">DATE</span>
                                            <span className="opacity-70">{moment(invoiceView.invoiceDate).format('DD MMM YYYY')}</span>
                                        </div>
                                        {invoiceView.gstin ?
                                        <div className="d-flex flex-column flex-root">
                                            <span className="font-weight-bolder mb-2">GSTIN.</span>
                                            <span className="opacity-70">#{invoiceView.gstin}</span>
                                        </div>:''}
                                        <div className="d-flex flex-column flex-root">
                                            <span className="font-weight-bolder mb-2">INVOICE NO.</span>
                                            <span className="opacity-70">#{invoiceView.invoiceId}</span>
                                        </div>
                                        <div className="d-flex flex-column flex-root" style={{ width: '33%' }}>
                                            <span className="font-weight-bolder mb-2">INVOICE TO.</span>
                                            <span className="opacity-70">
                                                {invoiceView.customerName}<br />
                                                {invoiceView.address1Value}<br />{invoiceView.address2Value} <br />{invoiceView.countryvalue}<br />
                                            </span>
                                        </div>
                                    </div>
                                    <div className="justify-content-center py-8 px-8 py-md-10 px-md-0">
                                        <div className="table-responsive border-top">
                                            <table className="table m-0">
                                                <thead>
                                                    <tr>
                                                        <th className="pl-0 font-weight-bold text-muted  text-uppercase">Description</th>
                                                        <th className="text-right pr-0 font-weight-bold text-muted text-uppercase">Amount</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr className="font-weight-boldest">
                                                        <td className="pl-0 align-top font-weight-boldest">{invoiceView.item}</td>
                                                        <td width="30%" className="border-left p-0 text-right">
                                                            <table className="table m-0 ">
                                                                <tbody>
                                                                    {invoiceView.vat ? <tr className="font-weight-boldest">
                                                                       <td className="pr-0  text-right border-top-0"> {invoiceView.currencyValue}{invoiceView.amount}</td>
                                                                    </tr>:''}
                                                                    {invoiceView.vat ? <tr className="font-weight-boldest">
                                                                        <td className="pr-0  text-right">GST(18%) : {invoiceView.currencyValue}{invoiceView.vat}</td>
                                                                    </tr>:''}
                                                                    <tr className="font-weight-boldest">
                                                                        <td className="pr-0  text-right">Total :{invoiceView.currencyValue}{invoiceView.total > 0 ? invoiceView.total.toFixed(2):invoiceView.total}</td>
                                                                    </tr>
                                                                </tbody></table>
                                                         </td>
                                                            
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                        <div className="justify-content-center border-top pt-3">
                                            <div className="d-flex justify-content-between flex-wrap">
                                                <span className="my-3 font-weight-bold">Social Scape Tech LLP, C-2 Panchsheel Colony, JPR, RJ (IN)</span>
                                                <button className="btn btn-primary m-0 font-weight-bold" onClick={downloadInvoicePDF(invoiceInfo.invoiceId, invoiceView.invoiceId)}>Download Invoice</button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div> : ''}
                </Modal.Body>
            </Modal>
        )
    }
}
