import React, { Component } from 'react'
import moment from 'moment';
import saveAs from 'file-saver';
import { ACTION_CANCEL_SUBSCRIPTION, TAGGSHOP_DOWNLOAD_INVOICE_PDF_URL } from '../../../../actions/sceneActions'
import { TOAST_SUCCESS, TOAST_ERROR } from '../../../../utils'
import { HOST_URL, HOST_URL_TAGGSHOP} from '../../../../actions/api'
import PaymentMethod from './PaymentMethod'
import InvoicePopup from './InvoicePopup'
import {Circles} from 'react-loader-spinner';
import { connect } from 'react-redux';
import { togglePriceSection } from '../../../../actions/show_modal_Action';
import Swal from 'sweetalert2'
import Subscription_cancel_popup from './Subscription_cancel_popup';
import Cancellation from './Cancellation/Index';
 class ProductSubscriptions extends Component {
    state = { showInvoice: false, invoiceInfo: [] , calcelSubs:false, planExpireDate:'', showCancellation:false , productId:'' , planId:'' , expireDate:''}

    openInvoicePopup = (item) => event => {
        this.setState({ showInvoice: true, invoiceInfo: item })
    }
    closeInvoicePopup = () => {
        this.setState({ showInvoice: false, invoiceInfo: [] })
    }

 openCancellationPopup = (productId, planId, expireDate) =>{
    this.setState({showCancellation:true , productId:productId , planId:planId , expireDate  })
 }

    cancelsubscription = (productId, planId, expireDate) => {
        Swal.fire({
            title: "Confirm?",
            text:  'Do you want to cancel your subscription with Tagshop?',
            icon: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            cancelButtonText: "No",
        }).then((result) => {
            if (result.value) {
                this.setState({calcelSubs:true, planExpireDate:expireDate  > 0 ? moment.unix(expireDate).format('Do MMM YYYY'):``})
                ACTION_CANCEL_SUBSCRIPTION(productId, planId).then((res)=>{
                     window.location.href = `${HOST_URL}walls/home`;
                });
            }
            else if (result.dismiss === "cancel") { }
        });
    }


   


    downloadInvoicePDF = (invoiceId, invoiceId2) => event => {
        TAGGSHOP_DOWNLOAD_INVOICE_PDF_URL(invoiceId).then((response) => {
            const file = new Blob([response.data], { type: 'application/pdf' });
            const fileURL = URL.createObjectURL(file);
            saveAs(file, `Invoice-${invoiceId2}.pdf`);
        }).catch((error) => {
 
        });
    }

    render() {
        const { productSubscribData , authorized} = this.props;
        const { showInvoice, invoiceInfo, calcelSubs , planExpireDate, showCancellation, productId , planId} = this.state;
        return (
            <div id="subscription_table" className="subscription_table">
                <h4 className="text-dark font-weight-bold my-1 mb-5">All Subscription</h4>
                <div className="subscription-plan-table table-responsive bg-white rounded accordion">
                    <table className="table plan_sub_table">
                        <thead>
                            <tr>
                                <th scope={'col'}>Product Plan</th>
                                <th scope={'col'}>Pricing</th>
                                <th scope={'col'}>Expiry at</th>
                                <th scope={'col'}>Status</th>
                                <th scope={'col'} align={'right'} style={{ textAlign: 'right' }}>Plan Action</th>
                            </tr>
                        </thead>
                    </table>

                    {productSubscribData ? productSubscribData.map((item, index) => (
                        
                       item.Product.id !==1 && item.userProduct ? <>
                        <div className="accordion-item border-0" key={item.Product.id}>
                            <table className="table plan_sub_table m-0">
                                <tbody>
                                    <tr>
                                        <td>
                                            <div className="d-flex align-items-center flex-nowrap">
                                                {item.invoices && item.invoices.length > 0 ? 
                                                    <div className="flex-shrink-0 mr-4" id="headingOne">
                                                        <button className="accordion-link mr-4 p-0 bg-transparent shadow-none accoudion-arrow border-0 collapsed" type="button" data-bs-toggle="collapse" data-bs-target={`#allInvoice_of_${item.Product.id}`} aria-expanded="false" aria-controls="taggshop-accordion">
                                                                <span className="svg-icon svg-icon-md">
                                                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                                        <g id="Group_4485" data-name="Group 4485" transform="translate(10922 -4712)">
                                                                            <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-10922 4712)" fill="none" opacity="0"></rect>
                                                                            <g id="Group" transform="translate(-10948.406 4695.088)">
                                                                                <path id="Path" d="M42.129,21.333a1.094,1.094,0,0,0-1.559,0l-6.837,6.837a1.094,1.094,0,0,0,0,1.559l6.837,6.837a1.1,1.1,0,1,0,1.559-1.559l-6.044-6.072,6.044-6.072A1.094,1.094,0,0,0,42.129,21.333Z" fill="#545454"></path>
                                                                            </g>
                                                                        </g>
                                                                    </svg>
                                                                </span>
                                                            </button>
                                                        </div>
                                                :""}
                                                <div className="flex-shrink-0 mr-4">
                                                    <div className="symbol symbol-40">
                                                        <img alt="Pic" src={item.Product.logo} />
                                                    </div>
                                                </div>
                                                <div className="d-flex align-items-center justify-content-between flex-wrap mt-2">
                                                    <div className="mr-3">
                                                        <h5 className="d-flex align-items-center font-weight-bold">{item.Product.name}</h5>
                                                        <span className="font-weight-normal">{item.userProduct.name}</span>
                                                        {item.userProduct.is_trial && ![1,57].includes(item.userProduct.planId) ? <span className="ms-2 font-weight-normal">- Free Trial</span>:''}
                                                    </div>
                                                </div>
                                            </div>
                                        </td>
                                        <td>
                                            <p className="text-dark-75 m-0">${item.userProduct.price}</p>
                                        </td>
                                        <td>
                                            <div className="d-flex flex-column text-left">
                                                <p className="mb-0">{ item.userProduct.expiresAt  > 0 ? moment.unix(item.userProduct.expiresAt).format('Do MMM YYYY'):`--`}</p>
                                            </div>
                                        </td>
                                        <td>{item.userProduct.expiresAt  > 0 ? moment(new Date(), 'YYYY.MM.DD').unix() < item.userProduct.expiresAt ?
                                            <span className="label font-weight-bold label-xl label-inline label-light-success plan_status">
                                                Active
                                            </span> :
                                            <span className="label font-weight-bold label-xl label-inline label-light-warning plan_status">
                                                Expired 
                                            </span>:
                                               <span className="label font-weight-bold label-xl label-inline label-light-success plan_status">
                                             Active
                                            </span>}
                                        </td>
                                        <td align={'right'}>
                                            <div className="upgrade-cancel-link">

                                                <button className="btn btn-primary btn-sm upgPlan" onClick={()=> this.props.togglePriceSection(true, item.Product.id)} >
                                                    <span className="navi-text upgPlan" >
                                                    {item.userProduct.is_trial ? 'Upgrade Plan' :'Change Plan'}</span>
                                                </button> 
                                                 {!(item.userProduct.cancelSubscription || item.userProduct.is_trial || item.userProduct.lanId=='57' || (authorized?.userData?.user_data?.sessionData?.createFrom && authorized?.userData?.user_data?.sessionData?.createFrom.toLowerCase() == 'shopify') || !(item.invoices && item.invoices.length > 0)) ? 
                                                <button className="cancelSubs ms-3 cancelsubscription" data-src={`${HOST_URL_TAGGSHOP}/transactions/cancelsubscription/${authorized && authorized.userData && authorized.userData.user_data && authorized.userData.user_data.user.ownerId ? authorized.userData.user_data.user.ownerId : ''}/${item.userProduct.product_id}`} id='cancelsubscription' data-action="cancelsubscription" data-on={authorized && authorized.userData && authorized.userData.user_data && authorized.userData.user_data.user.ownerId ? authorized.userData.user_data.user.ownerId : ''} data-id={item.userProduct.product_id} data-date={ item.userProduct.expiresAt  > 0 ? moment.unix(item.userProduct.expiresAt).format('Do MMM YYYY'):``} 
                                                onClick={e => this.openCancellationPopup(item.userProduct.product_id, item.userProduct.planId, item.userProduct.expiresAt)}>
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" viewBox="0 0 24 24">
                                                        <g id="Group_6518" data-name="Group 6518" transform="translate(-1627 -349)">
                                                            <path id="Icon_ionic-ios-close" data-name="Icon ionic-ios-close" d="M17.322,16.159l3.486-3.48a.816.816,0,0,0-1.155-1.153l-3.486,3.48-3.486-3.48a.816.816,0,1,0-1.155,1.153l3.486,3.48-3.486,3.48a.816.816,0,1,0,1.155,1.153l3.486-3.48,3.486,3.48a.816.816,0,0,0,1.155-1.153Z" transform="translate(1622.774 344.77)" fill="#545454" opacity="0.5"></path>
                                                        </g>
                                                    </svg>
                                                </button> :''}
                                            </div>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>

                            <div className="accordion-body p-0">
                                <div id={`${item.invoices && item.invoices.length > 0 ? `allInvoice_of_${item.Product.id}` : ''}`} className="accordion-collapse invoice-data collapse">
                                    <table className="table m-0">
                                        <thead>
                                            <tr>
                                                <th>Date</th>
                                                <th>Invoice Id</th>
                                                <th>Plan</th>
                                                <th>Amount</th>
                                                <th>Status</th>
                                                <th align={"right"} style={{ textAlign: "right" }}>Action</th>
                                            </tr>
                                        </thead>

                                        <tbody>
                                            {item.invoices ? item.invoices.map((invoiceItem, index) => (
                                                <tr key={invoiceItem.invoiceId}>
                                                    <td>{moment(invoiceItem.invoiceDate).format('DD MMM YYYY')}</td>
                                                    <td>{invoiceItem.invoiceView.invoiceId}</td>
                                                    <td>{invoiceItem.plan}</td>
                                                    <td>{invoiceItem.invoiceView.currencyValue}{invoiceItem.amount}</td>
                                                    <td>{invoiceItem.status ?
                                                        <span className="label font-weight-bold label-xl label-inline label-light-success plan_status">Paid</span>
                                                        : ''}
                                                    </td>
                                                    <td align={"right"}>
                                                        <button type="button" className="btn btn-clean btn-sm btn-icon " onClick={this.openInvoicePopup(invoiceItem)}>
                                                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                                <g id="View_4498" data-name="View 4498" transform="translate(10872 -4712)">
                                                                    <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-10872 4712)" fill="none" opacity="0"></rect>
                                                                    <g id="View_4497" data-name="View 4497" transform="translate(-10871.996 4712.004)">
                                                                        <path id="Path_5232" data-name="Path 5232" d="M21.87,11.5c-.64-1.11-4.16-6.68-10.14-6.5C6.2,5.14,3,10,2.13,11.5a1,1,0,0,0,0,1c.63,1.09,4,6.5,9.89,6.5h.25c5.53-.14,8.74-5,9.6-6.5A1,1,0,0,0,21.87,11.5ZM12.22,17c-4.31.1-7.12-3.59-8-5,1-1.61,3.61-4.9,7.61-5,4.29-.11,7.11,3.59,8,5-1.03,1.61-3.61,4.9-7.61,5Z" fill="#545454"></path>
                                                                        <path id="Path_5233" data-name="Path 5233" d="M12,8.5A3.5,3.5,0,1,0,15.5,12,3.5,3.5,0,0,0,12,8.5Zm0,5A1.5,1.5,0,1,1,13.5,12,1.5,1.5,0,0,1,12,13.5Z" fill="#545454"></path>
                                                                    </g></g></svg>
                                                        </button>
                                                        <button type="button" className="btn btn-clean btn-sm btn-icon ms-2" onClick={this.downloadInvoicePDF(invoiceItem.invoiceId, invoiceItem.invoiceView.invoiceId)} >
                                                        {/* <Circles type="ThreeDots" color="#333333" height={18} width={18} /> */}
                                                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                                <g id="Download_6506" data-name="Download 6506" transform="translate(-23412 -236)">
                                                                    <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(23412 236)" fill="none"></rect>
                                                                    <g id="noun-download-4879420" transform="translate(23332.367 197.581)">
                                                                        <path id="Path_6615" data-name="Path 6615" d="M84.068,44.107a.609.609,0,0,1,.9-.822l2.745,3v-9.4a.609.609,0,0,1,1.217,0v9.4l2.745-3a.609.609,0,0,1,.9.822l-3.8,4.154a.608.608,0,0,1-.9,0ZM97.45,45.512a.609.609,0,0,0-.609.609v5.552h-17.8V46.121a.609.609,0,0,0-1.218,0V52.28a.608.608,0,0,0,.609.609H97.45a.608.608,0,0,0,.609-.608v-6.16a.609.609,0,0,0-.609-.609Z" transform="translate(3.814 4.128)" fill="#545454" stroke="#545454" strokeWidth="0.8" fillRule="evenodd"></path>
                                                                    </g></g></svg>
                                                        </button>
                                                    </td>
                                                </tr>

                                            )) : ''}

                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        
                        </div> </>:''

                    )) : ''}
                </div>
                {/* <PaymentMethod /> */}
                <InvoicePopup showInvoice={showInvoice} invoiceInfo={invoiceInfo} downloadInvoicePDF={this.downloadInvoicePDF} closeInvoicePopup={this.closeInvoicePopup} />
               {calcelSubs ? <Subscription_cancel_popup planExpireDate={planExpireDate} closePopup={()=>{this.setState({calcelSubs:false})}}/>:''}
               {showCancellation ? <Cancellation handleParentStatus={()=>{this.setState({showCancellation:false})}} productId={productId} planId={planId}/> :''} 

            </div>


        )
    }
}


const mapStateToProps = state => {
    return { }
  }
  
  const mapDispatchToProps = dispatch => {
    return { 
        togglePriceSection: (state, id) => dispatch(togglePriceSection(state, id)),
    }
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(ProductSubscriptions);