const ContinueToCancel = ({ pageStatus, acceptOfferData, handleParentStatus }) => {
    const navigate = useNavigate()
    const [pageData, setPageData] = useState();

    const planDetails = () =>{ 
        handleParentStatus(false);
        navigate(`/walls/home`);
    }

    useEffect(() => {
        const res = dataJson.filter((value) => value.type == pageStatus)
        setPageData(res.length > 0 ? res[0] : res)
    }, [pageStatus]);

    return (
        <>
            {pageData && Object.keys(pageData).length > 0 ?
                <div className='small_container'>
                    <div className="bg-blue rounded thankYou_content_box">
                        <div className="text-center">
                            <p className="my-8">{pageData.icon} </p>
                            <h3 className='mb-5'>{pageData.heading}
                                {(pageStatus == 3 && Object.keys(acceptOfferData).length > 0 )&& <><del className='text-secondary'>{acceptOfferData.currency == 'inr' ? '' : '$'}{acceptOfferData.beforePercentage}</del> <span className='text-danger'>{acceptOfferData.currency == 'inr' ? '' : '$'}{acceptOfferData.afterPercentage}/{acceptOfferData.interval}</span></>}
                            </h3>
                            <p className="mb-8">{pageData.text}</p>

                            <button className="btn btn-primary btn-lg px-8" onClick={() => planDetails()}>Go To Home</button>

                        </div>
                    </div>
                </div> : null}</>
    )
}

export default ContinueToCancel